<template>
  <div class="flex">
    <aside>
        <Sidebar :list="list" base="multi"/>
    </aside>
    <section>
        <router-view :route="route"/>
    </section>
  </div>
</template>

<script>
import { mapGetters } from 'vuex'
import Sidebar from '@/components/common/Sidebar';
export default {
  data () {
    return {
        list: [
            {
                txt: '视频监控',
                url: 'video'
            },
            {
                txt: '工程参数',
                url: 'parameter'
            },
            {
                txt: '工作实时柱状图',
                url: 'line'
            },
            {
                txt: '工程预警',
                url: 'warn'
            },
            {
                txt: '三压力监测',
                url: 'watch'
            },
            {
                txt: '时效统计',
                url: 'time'
            },
            {
                txt: '钻头统计',
                url: 'bit'
            },
            {
                txt: '复杂率统计',
                url: 'complex'
            },
            {
                txt: '施工进度统计',
                url: 'count'
            }
        ]
    }
  },

  components: {
      Sidebar
  },

  computed: {
      ...mapGetters({
      route: 'route'
    }),
  },

  mounted: function() {},

  methods: {}
}
</script>

<style lang='scss' scoped>
.flex {
    height: 100%;
}
aside {
    width: 260px;
}
section {
    width: calc(100% - 260px);
}
</style>



